body {
  background-color: #222629;
  color: #86C232;
  font-family: monospace;
  font-size: 14px;
  margin: 0px; }

* {
  box-sizing: border-box; }

article {
  background-color: #222629;
  background-image: repeating-linear-gradient(rgba(107, 110, 112, 0.1), rgba(107, 110, 112, 0.1) 1px, transparent 1px, transparent 4px); }

.hidden-header {
  position: absolute;
  z-index: -20; }

#navbar {
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0 20px 7px 20px;
  width: 100vw;
  background-color: rgba(35, 39, 42, 0.8); }
  #navbar ul {
    list-style: none;
    display: flex;
    padding: 0px;
    margin: 0px; }
  #navbar li {
    display: inline-block;
    margin: 0px;
    padding: 0px; }
  #navbar a {
    display: inline-block;
    text-decoration: none;
    background: #222629;
    color: #86C232;
    padding: 10px 20px;
    margin-right: 7px;
    border-right: 2px solid #86C232;
    border-left: 2px solid #86C232;
    border-bottom: 2px solid #86C232; }
  #navbar a:hover, #navbar a:focus {
    background-color: #61892F;
    color: white;
    box-shadow: inset 1px 1px 5px #222629; }

#a-welcome {
  color: white;
  font-weight: bold; }

#welcome-section {
  box-sizing: border-box;
  height: 100vh;
  width: 75%;
  margin: 0px auto; }

#welcome-grid {
  background: none;
  height: 100%;
  margin: 0px;
  padding: 0px;
  display: grid;
  grid-template: "greet greet name ." ". tagline tagline tagline";
  grid-gap: 5px; }
  #welcome-grid span {
    display: inline-block;
    text-align: center; }

#name {
  font-size: 45px;
  margin: 0px;
  padding: 10px 0px;
  text-align: center;
  grid-area: name;
  align-self: end;
  background: #6B6E70;
  color: #222629; }

#greeting {
  font-size: 20vh;
  grid-area: greet;
  align-self: end;
  background: #86C232;
  color: #222629; }

#tagline {
  padding: 10px 0px;
  font-weight: bold;
  font-size: 20px;
  grid-area: tagline;
  align-self: start;
  background: #61892F;
  color: #222629; }

#projects {
  min-height: 100vh;
  background-color: #61892F;
  background-image: repeating-linear-gradient(rgba(134, 195, 50, 0.1), rgba(134, 195, 50, 0.1) 1px, transparent 1px, transparent 4px);
  padding: 50px 10px 10px 10px; }

.proj-flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center; }
  .proj-flex a {
    text-decoration: none;
    border-radius: 50%; }

.project-tile {
  position: relative;
  background-position: center;
  background-size: auto 300px;
  background-repeat: no-repeat;
  background-color: rgba(107, 110, 112, 0.6);
  width: 300px;
  height: 300px;
  margin: 8px;
  border: 10px solid #222629;
  border-radius: 50% 50% 5px 5px;
  box-shadow: 1px 1px 3px #474B4F;
  vertical-align: center; }
  .project-tile .project-tile-info {
    display: inline-block;
    position: absolute;
    bottom: -10px;
    left: -10px;
    width: 300px;
    padding: 10px;
    background-color: #222629;
    color: #86C232;
    border-radius: 5px; }
    .project-tile .project-tile-info .project-tile-title {
      text-align: center;
      font-weight: bold; }
    .project-tile .project-tile-info .project-tile-links {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      flex-wrap: wrap; }
      .project-tile .project-tile-info .project-tile-links .internal-example {
        cursor: pointer; }
      .project-tile .project-tile-info .project-tile-links a, .project-tile .project-tile-info .project-tile-links .internal-example {
        text-align: center;
        box-sizing: border-box;
        padding: 5px;
        max-width: 300px;
        height: 45px;
        flex-grow: 1;
        border-top: 2px solid #6B6E70;
        border-right: 2px solid #6B6E70;
        color: white;
        background-color: #222629;
        border-radius: 0; }
      .project-tile .project-tile-info .project-tile-links a:active, .project-tile .project-tile-info .project-tile-links .internal-example:active {
        color: white; }
      .project-tile .project-tile-info .project-tile-links a:first-child, .project-tile .project-tile-info .project-tile-links .internal-example:first-child {
        border-radius: 0 0 0 5px; }
      .project-tile .project-tile-info .project-tile-links a:last-child, .project-tile .project-tile-info .project-tile-links .internal-example:last-child {
        border-radius: 0 0 5px 0;
        border-right: none; }
      .project-tile .project-tile-info .project-tile-links a:only-child, .project-tile .project-tile-info .project-tile-links .internal-example:only-child {
        border-radius: 0 0 5px 5px;
        border-right: none; }

.project-tile:hover {
  box-shadow: none; }

.project-tile:hover .project-tile-info {
  height: 300px;
  border-radius: 25px 25px 5px 5px;
  background-color: rgba(34, 38, 41, 0.9);
  box-shadow: 1px 1px 3px #474B4F; }
  .project-tile:hover .project-tile-info .project-tile-title {
    display: block;
    width: 100%;
    border-bottom: 1px solid #86C232; }

footer {
  padding: 15px 10px;
  height: auto; }
  footer a {
    display: inline-block;
    margin: 5px 10px;
    text-decoration: none;
    color: #86C232; }
  footer a::before {
    content: "{ "; }
  footer a::after {
    content: " }"; }

@media only screen and (max-width: 650px) {
  #welcome-grid {
    grid-template: "." "greet" "." "name" "tagline";
    grid-template-rows: .25fr auto .25fr auto auto;
    align-content: center; } }

@media only screen and (max-width: 500px) {
  #navbar {
    border-bottom: 1px solid #61892F;
    width: 100vh;
    height: 25px;
    right: 100vw;
    transform: rotate(-90deg);
    transform-origin: 100% 0; }
    #navbar ul {
      width: 100%;
      flex-flow: row-reverse nowrap;
      justify-items: flex-end; }
    #navbar a {
      position: relative;
      top: -5px; }
    #navbar a:hover, #navbar a:focus {
      padding-top: 20px; }
  #projects, #skills {
    padding-left: 30px; }
  footer {
    padding-left: 30px; } }

@media only screen and (max-width: 380px) {
  #name {
    font-size: 30px; }
  #tagline {
    font-size: 10px; } }
