@import url('https://fonts.googleapis.com/css?family=Indie+Flower&display=swap');

$ltblue: #9198e5;

#inline-content {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  color: black;
  background-color: #555;
}

#quote-box{
  font-family: 'Indie Flower', cursive;
  background-color: white;
  background: repeating-linear-gradient(#fff, #fff 25px, $ltblue 26px, $ltblue 27px);
  background-position-y: 34px;
  height: 300px;
  width: 500px;
  padding: 0;
  margin-top: 50px;
}
#quote-header {
  position: relative;
  background: linear-gradient(#fff, #fff 33px, pink 35px, pink 36px);
  height: 36px;
  
  #author {
    position: absolute;
    left: 10px;
    top: 5px;
    font-size: 30px;
    font-weight: 700;
 }
 #author::after {
  content: " said:";
  font-size: 20px;
 }
 #tweet-quote {
  position: absolute;
  top: 4px;
  right: 45px;
  color: #555;
  font-size: 25px;
 }
 #tweet-quote:hover {
  color: #1da1f2;
 }
 #new-quote {
  position: absolute;
  top: 6px;
  right: 10px;
  background: none;
  border: none;
  padding: 0;
  color: #555;
  font-size: 22px;
 }
 #new-quote:hover {
  color: #ff0055;
  cursor: pointer;
 }
}
#text {
 position: relative;
 top: 10px;
 font-size: 22px;
 padding: 0 20px;
 line-height: 27px;
}
#text::before {
 content: "\"";
}
#text::after {
 content: "\"";
}

.rotate {
 transform: rotate(12deg);
}
.rotate:hover {
 transform: rotate(-12deg);
}